import React from "react";

const GateInfo: React.FC = () => {
  const gateData = {
    refreshTime: "12:34 PM",
    status: "Active",
    condition: "Good",
    openCloseState: "Closed",
    temperature: "35°C",
    voltage: "220V",
  };

  const tableData = [
    {
      label: "Refresh Time",
      value: gateData.refreshTime,
      icon: "⏱️",
    },
    {
      label: "Status",
      value: gateData.status,
      icon: "✅",
    },
    {
      label: "Condition",
      value: gateData.condition,
      icon: "⚙️",
    },
    {
      label: "Open/Close",
      value: gateData.openCloseState,
      icon: "🚪",
    },
    {
      label: "Temperature",
      value: gateData.temperature,
      icon: "🌡️",
    },
    {
      label: "Voltage",
      value: gateData.voltage,
      icon: "🔋",
    },
  ];

  return (
    <div className="w-full mt-6 bg-white rounded-xl shadow-lg overflow-hidden">
      <table className="w-full">
        <thead className="bg-gray-50">
          <tr>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Thông tin
            </th>
            <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
              Giá trị
            </th>
          </tr>
        </thead>
        <tbody className="divide-y divide-gray-200">
          {tableData.map((row, index) => (
            <tr key={index} className="hover:bg-gray-50 transition duration-200">
              <td className="px-6 py-4 flex items-center gap-3">
                <span className="text-xl">{row.icon}</span>
                <span className="text-sm text-gray-700">{row.label}</span>
              </td>
              <td className="px-6 py-4">
                <span className="text-sm font-semibold text-gray-900">
                  {row.value}
                </span>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default GateInfo;