import React from "react";

const OperationRecord: React.FC = () => {
  const records = [
    {
      time: "2025-01-26 14:32:45",
      operator: "John Doe",
      manipulation: "Opened gate",
      status: "Success",
    },
    {
      time: "2025-01-26 14:28:10",
      operator: "Jane Smith",
      manipulation: "Closed gate",
      status: "Success",
    },
    {
      time: "2025-01-26 14:15:22",
      operator: "Mike Brown",
      manipulation: "Restarted system",
      status: "Failed",
    },
    {
      time: "2025-01-26 14:00:00",
      operator: "Anna Lee",
      manipulation: "Cleared error",
      status: "Success",
    },
  ];

  return (
    <div className="p-6 bg-gray-100 rounded-xl shadow-lg">
      <h2 className="text-xl font-bold text-gray-800 mb-4">Operation Record</h2>
      <div className="overflow-x-auto">
        <table className="w-full text-left border-collapse">
          <thead>
            <tr className="bg-gray-200 text-gray-600 uppercase text-sm leading-normal">
              <th className="py-3 px-6">Time</th>
              <th className="py-3 px-6">Operator</th>
              <th className="py-3 px-6">Manipulation</th>
              <th className="py-3 px-6">Status</th>
            </tr>
          </thead>
          <tbody className="text-gray-700 text-sm font-light">
            {records.map((record, index) => (
              <tr
                key={index}
                className="border-b border-gray-200 hover:bg-gray-100"
              >
                <td className="py-3 px-6 whitespace-nowrap">{record.time}</td>
                <td className="py-3 px-6 whitespace-nowrap">{record.operator}</td>
                <td className="py-3 px-6 whitespace-nowrap">{record.manipulation}</td>
                <td
                  className={`py-3 px-6 whitespace-nowrap font-medium ${
                    record.status === "Success"
                      ? "text-green-500"
                      : "text-red-500"
                  }`}
                >
                  {record.status}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default OperationRecord;
