'use client'

import { useState, useEffect } from 'react'
import LiveIcon from '@/share/icon/live'
import { Circle } from 'lucide-react'
import { cameraFeeds } from '@/data/videos'
import CameraStream from '@/components/camera_frame'
import HLSStream from '@/components/Hls'
import HardwareControl from '@/components/control_hardware'
import GateInfo from '@/components/gate_condition'
import OperationRecord from '@/components/operator'

export default function SecurityCameraGrid() {
  const [activeCamera, setActiveCamera] = useState<number | null>(null)
  
  // Reset active camera after 10 seconds of inactivity
  useEffect(() => {
    if (activeCamera === null) return
    
    const timer = setTimeout(() => setActiveCamera(null), 10000)
    return () => clearTimeout(timer)
  }, [activeCamera])

  const activeCamerasCount = cameraFeeds.filter((cam) => cam.isActive).length

  // Camera feed renderer based on camera type
  const renderCameraFeed = (camera: typeof cameraFeeds[0]) => {
    const baseClassNames = "w-full h-full object-cover rounded-md border-2 border-[#444D56]"
    
    switch (camera.id) {
      case 1:
        return <CameraStream sseUrl="http://160.22.122.122:8001/sse" className={baseClassNames} />
      case 2:
        return <HLSStream videoUrl="http://160.22.122.122:8080/hls/stream.m3u8" className={baseClassNames} />
      default:
        return (
          <video
            className={baseClassNames}
            controls
            controlsList="nodownload nofullscreen"
          >
            <source src={camera.videoUrl} type="video/mp4" />
            Trình duyệt của bạn không hỗ trợ thẻ video.
          </video>
        )
    }
  }

  return (
    <div className="min-h-screen rounded-lg bg-white p-4">
      {/* Header with camera status and live button */}
      <header className="flex items-center justify-between mb-4 text-gray-700">
        <div className="flex items-center gap-2">
          <Circle className="w-2 h-2 fill-green-500 text-green-500" />
          <span className="text-sm">{activeCamerasCount} Camera Active</span>
        </div>
        <button className="hover:bg-gray-800 p-2 rounded-full transition-colors">
          <LiveIcon />
        </button>
      </header>

      {/* Camera grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-2 gap-4">
        {cameraFeeds.map((camera) => (
          <div
            key={camera.id}
            className="relative transition-transform hover:scale-[1.01]"
            onMouseEnter={() => setActiveCamera(camera.id)}
            onMouseLeave={() => setActiveCamera(null)}
          >
            {/* Camera video container */}
            <div className="aspect-video bg-gray-800 rounded-lg overflow-hidden">
              {renderCameraFeed(camera)}
            </div>

            {/* Live indicator overlay */}
            <div className="absolute top-4 left-4 right-4 flex justify-end text-white">
              <div className="flex items-center gap-2 bg-black/30 px-2 py-1 rounded-full">
                <Circle className="w-2 h-2 fill-red-500 text-red-500 animate-pulse" />
                <span className="text-sm font-medium">Live</span>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* Control sections */}
      <div className="mt-16 mx-auto max-w-6xl">
        <div className="flex flex-col md:flex-row justify-between gap-6">
          <HardwareControl
            onUp={() => console.log('Camera up')}
            onStop={() => console.log('Camera stop')}
            onDown={() => console.log('Camera down')}
          />
        </div>
        
        {/* Additional information components */}
        <GateInfo />
        <OperationRecord />
      </div>
    </div>
  )
}