import React, { useState } from "react";

interface HardwareControlProps {
  onUp: () => void;
  onStop: () => void;
  onDown: () => void;
}

const HardwareControl: React.FC<HardwareControlProps> = ({
  onUp,
  onStop,
  onDown,
}) => {
  const [selectedOption, setSelectedOption] = useState("Menu");
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [inputValue, setInputValue] = useState("");

  const menuOptions = [
    { label: "Refresh", value: "refresh" },
    { label: "Restart", value: "restart" },
    { label: "Clear Error", value: "clearError" },
  ];

  const handleMenuSelect = (option: string) => {
    setSelectedOption(option);
    setIsMenuOpen(false);
    // Thực hiện hành động tương ứng
    switch (option) {
      case "refresh":
        alert("Refreshed");
        break;
      case "restart":
        alert("Restarted");
        break;
      case "clearError":
        alert("Error Cleared");
        break;
      default:
        break;
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(e.target.value);
  };

  return (
    <div className="flex flex-col items-center w-full gap-4 p-4">
      {/* Dòng đầu tiên */}
      <div className="flex flex-col md:flex-row justify-between items-center w-full gap-4">
        <button
          onClick={onUp}
          className="w-full md:w-1/3 bg-green-500 text-white py-4 rounded-lg font-medium text-lg shadow-lg hover:bg-green-600 transition duration-300 text-center"
        >
          Up
        </button>
        <button
          onClick={onStop}
          className="w-full md:w-1/3 bg-red-500 text-white py-4 rounded-lg font-medium text-lg shadow-lg hover:bg-red-600 transition duration-300 text-center"
        >
          Stop
        </button>
        <button
          onClick={onDown}
          className="w-full md:w-1/3 bg-yellow-500 text-white py-4 rounded-lg font-medium text-lg shadow-lg hover:bg-yellow-600 transition duration-300 text-center"
        >
          Down
        </button>
      </div>

      {/* Dòng thứ hai */}
      <div className="flex flex-col md:flex-row justify-between items-center w-full gap-4">
        {/* Nút Menu */}
        <div className="relative w-full md:w-1/3">
          <button
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            className="w-full bg-gray-500 text-white py-4 px-6 rounded-lg font-medium text-lg shadow-lg hover:bg-gray-600 transition duration-300 flex items-center justify-between"
          >
            {selectedOption}
            <span className="ml-2">▼</span>
          </button>
          {isMenuOpen && (
            <div className="absolute mt-2 w-full bg-white rounded-lg shadow-lg border border-gray-200 z-20">
              {menuOptions.map((option) => (
                <div
                  key={option.value}
                  className="px-4 py-2 text-sm text-gray-700 hover:bg-gray-100 cursor-pointer"
                  onClick={() => handleMenuSelect(option.label)}
                >
                  {option.label}
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Ô Input */}
        <input
          type="text"
          value={inputValue}
          onChange={handleInputChange}
          placeholder="Nhập giá trị..."
          className="w-full md:w-1/3 p-4 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-gray-700"
        />

        {/* Nút Open To */}
        <button className="w-full md:w-1/3 bg-blue-500 text-white py-4 px-6 rounded-lg font-medium text-lg shadow-lg hover:bg-blue-600 transition duration-300">
          Open To
        </button>
      </div>
    </div>
  );
};

export default HardwareControl;